
@import url('https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,300;0,400;0,600;1,600&family=Rubik:ital,wght@1,700&display=swap');


:root {
--primary:#093a21d3;
--background:#f5f8ff;

}

.primary 
{
color: var(--primary);

}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}


ul {
list-style-type: none;

}
a{
text-decoration: none;
color: #333;

}
.container {
max-width: 1240px;
margin: auto;


}


body {
  margin: 0;
  font-family: 'Rubik', 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


  h1 {

    font-size: 3,5rem;
    line-height: 1.2;
  }
  
  h2 {
  
  font-size: 2.4rem;
  line-height: 1.2;
  
  }
  
  h5 {
  
  font-size: 1.1rem;
  line-height: 1.2;
  
  }
  p {
  
  font-size: 1.2rem;
  
  }


.btn {
padding: 14px 32px;
border: 1px solid var(--primary);
background-color: var(--primary);
color:#fff;
border-radius: 24px 4px;
font-size: 1.1rem;
font-family: 600;
cursor: pointer;

}
.btn:hover {
box-shadow: rgb(0 0 0 / 15%) 0px 8px;

}
input {

padding: 12px 32px;
border: 1px solid var(--primary);
background: transparent;
border-radius: 24px 4px;
font-size: 1rem;
margin-right: .8rem;
font-family:'Rubik',sans-serif ;

}


  
@media screen and (max:768px) {
  


h1 {

  font-size: 3,5rem;
  line-height: 1.2;
}

h2 {

font-size: 2.4rem;
line-height: 1.2;

}

h5 {

font-size: 1.1rem;
line-height: 1.2;

}
p {

font-size: 1.2rem;

}
.btn{
width: 100%;
margin: 1rem 0;

}
input {

  width: 100%;
}

}
